import React from 'react';
import { 
  Shield, 
  Smartphone, 
  Laptop, 
  Monitor, 
  School, 
  User, 
  RefreshCw, 
  LogOut,
  MapPin,
  Calendar,
  BadgeCheck
} from 'lucide-react';

// --- MOCK DATA (Replace this with your actual DB data) ---
const MOCK_USER = {
  id: "USR-2024-8892",
  name: "Sarah Johnson",
  // Change this role to 'HEADMASTER', 'TEACHER', or 'STUDENT' to see design changes
  role: "TEACHER", 
  email: "sarah.j@elmind-school.com",
  avatar: "https://api.dicebear.com/7.x/avataaars/svg?seed=Sarah",
  joinedDate: "Aug 2021"
};

const SCHOOL_DETAILS = {
  name: "Elmind International Academy",
  address: "123 Knowledge Park, Tech City",
  code: "SCH-9901",
  logo: "https://api.dicebear.com/7.x/initials/svg?seed=EI&backgroundColor=0f172a", // Placeholder school logo
  image: "https://images.unsplash.com/photo-1523050854058-8df90110c9f1?q=80&w=1000&auto=format&fit=crop" // School building image
};

const LOGIN_HISTORY = [
  { id: 1, device: "MacBook Pro", os: "macOS Sonoma", ip: "192.168.1.45", date: "Today, 10:23 AM", type: "desktop", active: true },
  { id: 2, device: "iPhone 14", os: "iOS 17.2", ip: "10.0.0.12", date: "Yesterday, 8:15 PM", type: "mobile", active: false },
  { id: 3, device: "Windows PC", os: "Windows 11", ip: "172.16.0.5", date: "Nov 20, 2023", type: "desktop", active: false },
];

export default function ProfilePage() {
  
  // Helper to get color based on role
  const getRoleBadgeColor = (role) => {
    switch (role) {
      case 'HEADMASTER': return 'bg-purple-100 text-purple-700 border-purple-200';
      case 'TEACHER': return 'bg-blue-100 text-blue-700 border-blue-200';
      case 'STUDENT': return 'bg-emerald-100 text-emerald-700 border-emerald-200';
      default: return 'bg-gray-100 text-gray-700';
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 p-4 md:p-8 font-sans text-slate-800">
      <div className="max-w-5xl mx-auto space-y-6">
        
        {/* --- HEADER SECTION: User & Role --- */}
        <div className="bg-white rounded-2xl shadow-sm border border-gray-200 overflow-hidden">
          <div className="h-32 bg-gradient-to-r from-slate-800 to-slate-900 relative">
            {/* Abstract Background pattern */}
            <div className="absolute inset-0 opacity-20" style={{backgroundImage: 'radial-gradient(#cbd5e1 1px, transparent 1px)', backgroundSize: '20px 20px'}}></div>
          </div>
          
          <div className="px-6 pb-6 relative">
            <div className="flex flex-col md:flex-row items-start md:items-end -mt-12 mb-4 gap-4">
              {/* User Avatar */}
              <div className="relative">
                <img 
                  src={MOCK_USER.avatar} 
                  alt="Profile" 
                  className="w-24 h-24 rounded-2xl border-4 border-white shadow-md bg-white"
                />
                <div className="absolute bottom-0 right-0 w-6 h-6 bg-green-500 border-4 border-white rounded-full" title="Online"></div>
              </div>
              
              {/* Name and Badge */}
              <div className="flex-1 pt-2 md:pt-0">
                <div className="flex flex-wrap items-center gap-2 mb-1">
                  <h1 className="text-2xl font-bold text-gray-900">{MOCK_USER.name}</h1>
                  <span className={`px-3 py-0.5 text-xs font-semibold rounded-full border ${getRoleBadgeColor(MOCK_USER.role)}`}>
                    {MOCK_USER.role}
                  </span>
                </div>
                <div className="flex items-center gap-4 text-sm text-gray-500">
                  <span className="flex items-center gap-1"><BadgeCheck size={14} /> ID: {MOCK_USER.id}</span>
                  <span className="flex items-center gap-1"><Calendar size={14} /> Joined: {MOCK_USER.joinedDate}</span>
                </div>
              </div>

              {/* Quick Action Button */}
              <button className="mt-4 md:mt-0 px-4 py-2 bg-slate-900 text-white rounded-lg hover:bg-slate-800 transition-colors text-sm font-medium flex items-center gap-2">
                <RefreshCw size={16} />
                Reset Password
              </button>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          
          {/* --- LEFT COLUMN: School Details --- */}
          <div className="md:col-span-1 space-y-6">
            <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
              <div className="p-4 border-b border-gray-100 flex items-center gap-2">
                <School className="text-slate-500" size={20} />
                <h2 className="font-semibold text-gray-900">School Details</h2>
              </div>
              
              {/* School Image */}
              <div className="h-32 w-full bg-gray-200 relative overflow-hidden group">
                <img 
                  src={SCHOOL_DETAILS.image} 
                  alt="School Building" 
                  className="w-full h-full object-cover transition-transform duration-500 group-hover:scale-105"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent flex items-end p-4">
                  <img src={SCHOOL_DETAILS.logo} className="w-10 h-10 rounded bg-white/90 p-1" alt="Logo" />
                </div>
              </div>

              <div className="p-5 space-y-4">
                <div>
                  <label className="text-xs font-semibold text-gray-400 uppercase tracking-wider">Institution Name</label>
                  <p className="text-sm font-medium text-gray-900 mt-1">{SCHOOL_DETAILS.name}</p>
                </div>
                
                <div>
                  <label className="text-xs font-semibold text-gray-400 uppercase tracking-wider">School Code</label>
                  <p className="text-sm font-medium text-gray-900 mt-1 font-mono bg-gray-50 inline-block px-2 py-1 rounded border">{SCHOOL_DETAILS.code}</p>
                </div>

                <div className="flex items-start gap-2">
                  <MapPin className="text-gray-400 mt-0.5" size={16} />
                  <p className="text-sm text-gray-600 leading-relaxed">
                    {SCHOOL_DETAILS.address}
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* --- RIGHT COLUMN: Login History & Reset Options --- */}
          <div className="md:col-span-2 space-y-6">
            
            {/* Login History Panel */}
            <div className="bg-white rounded-xl shadow-sm border border-gray-200">
              <div className="p-4 border-b border-gray-100 flex justify-between items-center">
                <div className="flex items-center gap-2">
                  <Shield className="text-slate-500" size={20} />
                  <h2 className="font-semibold text-gray-900">Device History</h2>
                </div>
                <span className="text-xs text-gray-500 bg-gray-100 px-2 py-1 rounded-full">
                  Last 30 Days
                </span>
              </div>

              <div className="p-2">
                {LOGIN_HISTORY.map((session) => (
                  <div key={session.id} className="flex items-center gap-4 p-3 hover:bg-gray-50 rounded-lg transition-colors group">
                    {/* Icon based on device type */}
                    <div className={`p-2 rounded-lg ${session.active ? 'bg-blue-50 text-blue-600' : 'bg-gray-100 text-gray-500'}`}>
                      {session.type === 'mobile' ? <Smartphone size={20} /> : <Laptop size={20} />}
                    </div>

                    <div className="flex-1 min-w-0">
                      <div className="flex justify-between items-center mb-0.5">
                        <p className="text-sm font-medium text-gray-900 truncate">
                          {session.device} <span className="text-gray-400 font-normal mx-1">â€¢</span> {session.os}
                        </p>
                        {session.active && (
                          <span className="text-[10px] font-bold text-green-600 bg-green-50 px-2 py-0.5 rounded-full border border-green-100 uppercase tracking-wide">
                            Active Now
                          </span>
                        )}
                      </div>
                      <div className="flex items-center gap-3 text-xs text-gray-500">
                        <span>{session.ip}</span>
                        <span className="w-1 h-1 bg-gray-300 rounded-full"></span>
                        <span>{session.date}</span>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
              <div className="p-3 border-t border-gray-100 bg-gray-50 rounded-b-xl text-center">
                <button className="text-sm text-slate-600 hover:text-slate-900 font-medium flex items-center justify-center gap-2 w-full">
                  <LogOut size={14} /> Sign out of all other devices
                </button>
              </div>
            </div>

            {/* Danger Zone / Reset Options */}
            <div className="bg-white rounded-xl shadow-sm border border-red-100 overflow-hidden">
               <div className="p-4 bg-red-50/50 border-b border-red-100 flex items-center gap-2">
                <RefreshCw className="text-red-500" size={20} />
                <h2 className="font-semibold text-red-900">Account Reset Options</h2>
              </div>
              <div className="p-6">
                <p className="text-sm text-gray-600 mb-4">
                  Looking to reset your credentials? As a <span className="font-semibold lowercase">{MOCK_USER.role}</span>, resetting involves security verifications.
                </p>
                
                <div className="flex gap-3">
                  <button className="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg text-sm font-medium hover:bg-gray-50 transition-colors">
                    Reset Email
                  </button>
                  <button className="px-4 py-2 bg-red-600 text-white rounded-lg text-sm font-medium hover:bg-red-700 transition-colors shadow-sm">
                    Reset Password
                  </button>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  );
}